import React from 'react';

import { NavLink } from 'react-router-dom';
const Subnav = () => {
  return (
  <>
    <div className="max">
      <div className="container-fluid ">
        <div className="row">
          <div className="col-12  mx-auto max">
            <div id="nav">
                <nav className="navbar navbar-expand-lg navbar-light bg-light">
                  <div className="container-fluid">  
                  <button 
                    className="navbar-toggler" 
                    type="button" 
                    data-bs-toggle="collapse" 
                    data-bs-target="#navbarSupportedContent" 
                    aria-controls="navbarSupportedContent" 
                    aria-expanded="false" 
                    aria-label="Toggle navigation">
                    <span className="navbar-toggler-icon"></span>
                   </button>
                    <button  
                      className="navbar-toggler" 
                      type="button"    
                      data-toggle="collapse" 
                      data-target="#navbarSupportedContent" 
                      aria-controls="navbarSupportedContent" 
                      aria-expanded="false" 
                      aria-label="Toggle navigation">
                      <span className="navbar-toggler-icon"></span>
                    </button>
                   <div className="collapse navbar-collapse" id="navbarSupportedContent">
                     <ul className="navbar-nav me-auto mb-2 mb-lg-0">
                     <li className="nav-item">
                         <NavLink activeClassName="menu_active" className="nav-link" to="/"><span>Order Online</span></NavLink>
                       </li>
                       <li className="nav-item">
                         <NavLink activeClassName="menu_active" className="nav-link" to="/photo"><span>Gallery</span></NavLink>
                       </li>
                       <li className="nav-item">
                         <NavLink activeClassName="menu_active" className="nav-link " to="/overview"><span>OverView</span></NavLink>
                       </li>
                      
                       <li className="nav-item">
                         <NavLink activeClassName="menu_active" className="nav-link" to="/contect" ><span>Contect us</span></NavLink>
                       </li>
                       
                     </ul>
                   </div>
                  </div>
                </nav>
            </div>
          </div>
        </div>
      </div>
    </div>
  </>
  );
};

export default Subnav;